
.TodoItem {
    background: rgba( 255, 255, 255, 0.25 );
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 60px;
    border: 2px solid white;
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75px;
    width: 75px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 30px;
    background: rgba( 255, 255, 255, 0.25 );
    border-radius: 50%;
    font-weight: bold;
    font-size: 50px;
  }

  .Icon-check--active {
    color: white;
    background-color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: 18px;
    right: 20px;
    font-size: 50px;
  }
  .Icon-delete:hover {
    color: red;
  }
